
import { defineComponent } from 'vue'
import {
    IonSkeletonText,
    IonList,
    IonItem,
} from "@ionic/vue"
export default defineComponent({
    components: {
        IonSkeletonText,
        IonList,
        IonItem,
    },
    props: {
        itemCount: {
            type: Number,
            default: 10
        }
    },
    setup(props) {
        const items = []
        for(let x = 0; x < props.itemCount; ++x) {
            items.push(x)
        }
        return {
            items
        }
    },
})
